/* BulletPoints.module.css */

.react_topics {
margin-bottom: 20px;
border-radius: 4px;
border: 1px solid #ddd;
background-color: #f5f5f5;
padding: 20px;
position: relative;

}

.row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.column {
width: 50%;
padding: 10px;
}

.left {
    font-weight: bold;
    font-style: italic;
    font-size: 24px;
    text-align: left;
}

.right {
    font-weight: bold;
    font-style: italic;
    font-size: 18px;
    text-align: right;
}

.react_topics h2 {
font-size: 24px;
font-weight: bold;
margin-bottom: 10px;
background-color: #272626;
color: #fff;
padding: 10px;
border-radius: 4px;
}


.react_topics ul {
list-style-type: none;
margin: 0;
padding: 0;
}

.react_topics li {
    list-style-type: square;
    margin-bottom: 5px;
    position: relative;
    padding-left: 10px;
}
  
  
  