/* Resume.css */
.header {
    margin: 20px;
    max-width: 300px;
    float: center;
    /* position: relative; */
}

    .headshot {
        width: 100px; /* set the width of the image */
        height: 100px; /* set the height of the image */
        margin-right: 20px; /* set the right margin to create space between the image and other content */
        float: center; /* set the float property to left to make the image left-aligned */
        border-radius: 70%;
    }
    .name {
        font-size: 28px;
        font-weight: bold;
        margin-bottom: 100;
        float: center;
    }


.resume-container {
    margin-bottom: 20px;
    border-radius: 4px;
    border: 1px solid #ddd;
    background-color: #f5f5f5;
    padding: 20px;
    position: relative;
  }
  
  .education-item,

  .work-experience-item {
    margin-bottom: 40px;
  }
  
  h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    background-color: #272626;
    color: #fff;
    padding: 10px;
    border-radius: 4px;
  }
  
  h3 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  h4 {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  li {
    list-style-type: square;
    margin-bottom: 5px;
    position: relative;
    padding-left: 10px;
  }
  .college-container
  .college-logo {
    width: 60px; /* set the width of the image */
    height: 60px; /* set the height of the image */
    margin-right: 20px; /* set the right margin to create space between the image and other content */
    float: left; /* set the float property to left to make the image left-aligned */
  }